<template>
  <div class="row">
    <div class="col-12 mt-3">
      <div class="row">
        <div class="col-12 flex-x-between mb-3">
          <div class="flex-align-center">
            <el-button
              v-ripple.400
              type="primary"
              circle
              class="no-width flat"
              @click="$router.go(-1)"
            >
              <i class="bx bx-left-arrow-alt"></i>
            </el-button>
            <h5 class="my-0 ml-2">Create cargo</h5>
          </div>
          <div class="flex-align-center flex-end">
            <el-button
              @click="$router.go(-1)"
              type="primary"
              v-ripple.400
              class="flat"
            >
              Discard
            </el-button>
            <el-button
              :disabled="!startEditing"
              :loading="saving"
              v-ripple.400
              @click="Post"
              type="primary"
            >
              Save
            </el-button>
          </div>
        </div>
        <div class="col-lg-8">
          <div class="box">
            <p class="mt-0 mb-2 text-bold">Details</p>
            <label for="name" class="custom-label">Name</label>
            <el-input name="name" class="w-100 mb-3" v-model="supplier.name" />
            <div class="row">
              <div class="col-6">
                <label for="Address" class="custom-label">Address</label>
                <el-input
                  name="Address"
                  class="w-100 mb-3"
                  v-model="supplier.address"
                />
              </div>
              <div class="col-6">
                <label for="Phone" class="custom-label">Phone number</label>
                <el-input
                  name="Phone"
                  type="number"
                  class="w-100 mb-3"
                  v-model="supplier.telephone"
                />
              </div>
            </div>
            <div class="row">
              <div class="col-6">
                <label for="Phone" class="custom-label">Website</label>
                <el-input
                  name="Phone"
                  type="url"
                  class="w-100"
                  v-model="supplier.website"
                />
              </div>
              <div class="col-6">
                <label for="Facebook" class="custom-label">Facebook Page</label>
                <el-input
                  name="Facebook"
                  type="url"
                  class="w-100"
                  v-model="supplier.facebook_page"
                />
              </div>
            </div>
          </div>

          <div class="box mt-base">
            <p class="mt-0 mb-2 text-bold">Contact Person</p>
            <div class="row">
              <div class="col-6">
                <label for="name" class="custom-label">Name</label>
                <el-input
                  name="name"
                  class="w-100"
                  v-model="supplier.contact_person"
                />
              </div>
              <div class="col-6">
                <label for="Phone" class="custom-label">Phone number</label>
                <el-input
                  name="Phone"
                  type="number"
                  class="w-100"
                  v-model="supplier.cp_mobile"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      saving: false,
      supplier: {},
      loading: null,
      startEditing: false,
    };
  },
  watch: {
    supplier: {
      handler: function () {
        this.startEditing = true;
      },
      deep: true,
    },
  },
  created() {
    this.$store.commit("setIsReady", true);
  },
  beforeMount() {
    window.addEventListener("beforeunload", this.preventNav);
    this.$once("hook:beforeDestroy", () => {
      window.removeEventListener("beforeunload", this.preventNav);
    });
  },
  beforeRouteLeave(to, from, next) {
    if (this.startEditing) {
      this.$smalltalk
        .confirm(
          "Question",
          "Do you really want to leave? you have unsaved changes!"
        )
        .then(() => {
          next();
        })
        .catch(() => {
          next(false);
        });
    } else {
      next();
    }
  },
  methods: {
    preventNav(event) {
      if (!this.startEditing) return;
      event.preventDefault();
      event.returnValue = "";
    },
    Post() {
      this.saving = true;
      this.supplier.store = this.store.id;
      this.$axios
        .post("supplier/", { data: this.supplier })
        .then(() => {
          this.saving = false;
          this.$noti.display({
            color: "success",
            title: "Success",
            message: "Cool! supplier has been successfully updated.",
          });
          this.startEditing = false;
          this.$router.push("/suppliers");
        })
        .catch((err) => {
          this.saving = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
  },
  computed: {
    ...mapState(["store"]),
  },
};
</script>

